import {request} from '../../utlis/request.js';

// 
/**
 * @export
 * @param {object} data {
 * 		Token:String
 *		Timer:String | Number
 * } 
 * @return {Promise} 
 */
export function getUserInfo(data) {
	return request('/user/userinfo',data,'POST',{
		'Content-type': 'application/x-www-form-urlencoded',
		'sessiontoken': data
	})
}

